<template>
  <div>
    <el-row class="el-row-add" type="flex" align="middle" justify="center">
      <el-col :span="6" :push="1">
        <label>身份证号</label>
      </el-col>
      <el-col :span="18">
        <el-input placeholder="请输入身份证号"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" align="middle" justify="center">
      <el-col :span="6" :push="1">
        <label>就诊卡号</label>
      </el-col>
      <el-col :span="18">
        <el-input placeholder="可选填"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" align="middle" justify="center">
      <el-col :span="6" :push="1">
        <label>姓名</label>
      </el-col>
      <el-col :span="18">
        <el-input placeholder="请输入姓名"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" align="middle" justify="center">
      <el-col :span="6" :push="1">
        <label>电话号</label>
      </el-col>
      <el-col :span="18">
        <el-input placeholder="请输入电话号码"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" style="margin-top:30px">
      <el-col :push="1"
        ><el-switch v-model="input" active-text="设置为默认就诊人"> </el-switch
      ></el-col>
    </el-row>
    <div style="margin-top:100px">
      <el-button type="primary" style="width:100%">添加</el-button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      input: ""
    };
  }
};
</script>
<style scoped>
.el-row-add {
  margin-top: 10px;
}
</style>



